import {useEffect, useState} from "react";
import {ArcSeries, FlexibleWidthXYPlot} from "react-vis";

import {
    Alert,
    Avatar,
    Grid,
    IconButton, Link,
    List,
    ListItem,
    ListItemAvatar,
    ListItemText, Paper, SvgIcon,
    Tooltip,
    Typography
} from "@mui/material";

import AccountBalanceIcon from '@mui/icons-material/AccountBalance';
import LocalFireDepartmentIcon from '@mui/icons-material/LocalFireDepartment';
import CachedIcon from '@mui/icons-material/Cached';
import AttachMoneyIcon from '@mui/icons-material/AttachMoney';
import ShieldIcon from '@mui/icons-material/Shield';
import CalculateIcon from '@mui/icons-material/Calculate';
import WebAssetIcon from '@mui/icons-material/WebAsset';
import TwitterIcon from '@mui/icons-material/Twitter';
import TelegramIcon from '@mui/icons-material/Telegram';
import ShowChartIcon from '@mui/icons-material/ShowChart';
import InvertColorsIcon from '@mui/icons-material/InvertColors';

import axios from "axios";
import {useSelector} from "react-redux";

const NumberFormatter = new Intl.NumberFormat('en-US', {
    maximumSignificantDigits: 5
});

export default function ProjectStats(props) {

    const {project} = props;
    const {bnbPrice, projectsMap} = useSelector(state => state.divvy);

    const [percent, setPercent] = useState(0);
    const [angle, setAngle] = useState(0);
    const [infoJSON, setInfoJSON] = useState({});
    const [loading, setLoading] = useState(true);

    const listingMin = 0.01;//percentage

    const stats = projectsMap[project.contract]?.stats

    useEffect(() => {
        axios
            .get(`https://fegexchange.github.io/assets/blockchains/smartchain/assets/${project.contract}/info.json`)
            .then(resp => {
                setInfoJSON(resp.data);
                setLoading(false)
            })

    }, [project]);

    useEffect(() => {
        if (stats && stats.burnt && stats.supply) {
            setPercent(stats.burnt / stats.supply);
            setAngle(360 * (stats.burnt / stats.supply) * (Math.PI / 180))
        }
    }, [stats]);

    if (loading || !stats) {
        return <></>
    }

    return (<Grid container spacing={2}>

        {
            (stats.divvyBalance * ((stats.fbnb - stats.backing) / stats.liquidity)) < 2 && <Grid item xs={12}>
                <Alert severity="warning">Listing on the Divvy Dashboard doesn't guarantee the security of a project, as
                    always Do Your Own Research #DYOR</Alert>
            </Grid>
        }

        <Grid item xs={6}>
            <Grid container direction="column">
                <Grid item alignSelf="center">
                    <img
                        src={`https://fegexchange.github.io/assets/blockchains/smartchain/assets/${project.contract}/logo.png`}
                        alt={`${project.logo} Logo`}
                        style={{borderRadius: '50%', marginTop: 20, marginBottom: 20}}
                    />
                </Grid>
                <Grid item alignSelf="center">
                    <Grid container spacing={2} alignContent="center">

                        {
                            infoJSON.website && <Grid item>
                                <Tooltip title="Website">
                                    <IconButton
                                        href={infoJSON.website}
                                        target="_blank"
                                        style={{
                                            backgroundColor: project.primary,
                                            color: project.secondary
                                        }}
                                    >
                                        <WebAssetIcon/>
                                    </IconButton>
                                </Tooltip>
                            </Grid>
                        }

                        {
                            infoJSON.social && infoJSON.social.twitter && <Grid item>
                                <Tooltip title="Twitter">
                                    <IconButton
                                        href={infoJSON.social.twitter}
                                        target="_blank"
                                        style={{
                                            backgroundColor: project.primary,
                                            color: project.secondary
                                        }}
                                    >
                                        <TwitterIcon/>
                                    </IconButton>
                                </Tooltip>
                            </Grid>
                        }
                        {
                            infoJSON.social && infoJSON.social.telegram && <Grid item>
                                <Tooltip title="Telegram">
                                    <IconButton
                                        href={infoJSON.social.telegram}
                                        target="_blank"
                                        style={{
                                            backgroundColor: project.primary,
                                            color: project.secondary
                                        }}
                                    >
                                        <TelegramIcon/>
                                    </IconButton>
                                </Tooltip>
                            </Grid>
                        }
                        <Grid item>
                            <Tooltip title="Charts">
                                <IconButton
                                    href={`https://charts.fegex.com/charts/${project.contract}`}
                                    target="_blank"
                                    style={{
                                        backgroundColor: project.primary,
                                        color: project.secondary
                                    }}
                                >
                                    <ShowChartIcon/>
                                </IconButton>
                            </Tooltip>
                        </Grid>
                    </Grid>
                </Grid>
            </Grid>
        </Grid>

        <Grid item xs={6}>
            <Grid container spacing={2}>
                <Grid item xs={12}>
                    <Paper style={{padding: '10px 20px'}}>
                        <Typography color="primary"
                                    variant="h6">{NumberFormatter.format(stats.divvyBalance) + ` ${project.symbol}`}</Typography>
                        <Typography variant="caption">Divvy Fund Balance</Typography>
                    </Paper>
                </Grid>
                <Grid item xs={12}>
                    <Paper style={{padding: '10px 20px'}}>
                        <Typography color="primary"
                                    variant="h6">{NumberFormatter.format((stats.divvyBalance * (stats.fbnb - stats.backing) / stats.liquidity) || 0) + ` BNB`} -
                            ${NumberFormatter.format((stats.divvyBalance * ((stats.fbnb - stats.backing) / stats.liquidity) * bnbPrice) || 0) + ` USD`}</Typography>
                        <Typography variant="caption">Divvy Fund Value</Typography>
                    </Paper>
                </Grid>
                <Grid item xs={12}>
                    <Paper style={{padding: '10px 20px'}}>
                        <Typography color="primary"
                                    variant="h6">{NumberFormatter.format(project.divvyFund === 0 ? (stats.divvyBalance > (stats.supply * listingMin) ? stats.divvyBalance - (stats.supply * listingMin) : 0) : stats.divvyBalance - project.divvyFund) + ` ${project.symbol}`}</Typography>
                        <Typography variant="caption">Divvy Fund Reflections</Typography>
                    </Paper>
                </Grid>
                <Grid item xs={12}>
                    <Paper style={{padding: '10px 20px'}}>
                        <Typography color="primary"
                                    variant="h6">{NumberFormatter.format(project.divvyFund === 0 ? (stats.divvyBalance > (stats.supply * listingMin) ? (stats.divvyBalance - (stats.supply * listingMin)) * ((stats.fbnb - stats.backing) / stats.liquidity) : 0) : (stats.divvyBalance - project.divvyFund) * ((stats.fbnb - stats.backing) / stats.liquidity)) + " BNB"}</Typography>
                        <Typography variant="caption">Divvy Fund Reflections Value</Typography>
                    </Paper>
                </Grid>
            </Grid>
        </Grid>

        {
            stats.divvyBalance < (stats.supply * listingMin) && !project.bypassMin ? <Grid item xs={12} style={{marginTop: 30}}>
                <Paper style={{paddingTop: 20}} align="center">
                    <Typography>Pending Listing. To be considered for listing on Divvy Dashboard
                        send {NumberFormatter.format(stats.supply * listingMin)} {project.symbol} to the <Link
                            href="https://bscscan.com/address/0xb80267ea7fa368374ee4d4bf10044778232adefe"
                            target="_blank">Divvy Fund</Link></Typography>


                    <Grid item xs={12}>
                        <FlexibleWidthXYPlot
                            xDomain={[-5, 5]}
                            yDomain={[-5, 5]}
                            height={300}
                            style={{
                                transform: 'translate(-15px, 5px)',
                            }}
                        >
                            <ArcSeries
                                radiusDomain={[0, 10]}
                                center={{x: 0, y: 0}}
                                data={[
                                    {
                                        angle0: 0,
                                        angle: Math.PI * 2,
                                        opacity: 1,
                                        radius: 6.75,
                                        radius0: 6,
                                        color: '#eee',
                                    },
                                    {
                                        angle0: 0,
                                        angle: (360 * (stats.divvyBalance / (stats.supply * listingMin)) * (Math.PI / 180)),
                                        opacity: 1,
                                        radius: 8.5,
                                        radius0: 6,
                                        color: project.primary,
                                    },
                                ]}
                                colorType={'literal'}
                            />
                        </FlexibleWidthXYPlot>
                        <Typography
                            style={{
                                height: 0,
                                width: '100%',
                                textAlign: 'center',
                                top: -190,
                                position: 'relative',
                                fontSize: 30,
                                fontWeight: '700',
                                color: project.primary
                            }}
                        >
                            {NumberFormatter.format((stats.supply * listingMin) - stats.divvyBalance)}
                            <span
                                style={{
                                    fontSize: 12,
                                    fontWeight: '400',
                                    display: 'block',
                                }}
                            >UNTIL LISTED</span>
                        </Typography>
                    </Grid>
                </Paper>
            </Grid> : <>

                <Grid item xs={12}>
                    <Paper style={{padding: 20}}>
                        {infoJSON.name && infoJSON.symbol &&
                            <Typography variant="h5">{infoJSON.name} ({infoJSON.symbol})</Typography>}
                        {infoJSON.id && <Link href={infoJSON.explorer}><Typography
                            variant="caption">{infoJSON.id}</Typography></Link>}
                        {infoJSON.description &&
                            <Typography variant="body1" style={{marginTop: 20}}>{infoJSON.description}</Typography>}
                    </Paper>
                </Grid>

                <Grid item xs={12}>
                    <Grid container spacing={2}>
                        <Grid item xs={12}>
                            <Paper>
                                <Grid container>
                                    <Grid item xs={6}>
                                        <FlexibleWidthXYPlot
                                            xDomain={[-5, 5]}
                                            yDomain={[-5, 5]}
                                            height={300}
                                            style={{
                                                transform: 'translate(-15px, 5px)',
                                            }}
                                            // className={classes.xyplot}
                                        >
                                            <ArcSeries
                                                radiusDomain={[0, 10]}
                                                center={{x: 0, y: 0}}
                                                data={[
                                                    {
                                                        angle0: 0,
                                                        angle: Math.PI * 2,
                                                        opacity: 1,
                                                        radius: 6.75,
                                                        radius0: 6,
                                                        color: '#eee',
                                                    },
                                                    {
                                                        angle0: 0,
                                                        angle: angle,
                                                        opacity: 1,
                                                        radius: 8.5,
                                                        radius0: 6,
                                                        color: project.primary,
                                                    },
                                                ]}
                                                colorType={'literal'}
                                            />
                                        </FlexibleWidthXYPlot>
                                        <Typography
                                            style={{
                                                height: 0,
                                                width: '100%',
                                                textAlign: 'center',
                                                top: -190,
                                                position: 'relative',
                                                fontSize: 30,
                                                fontWeight: '700',
                                                color: project.primary
                                            }}
                                        >
                                            {(percent * 100).toFixed(2)}
                                            <span
                                                style={{
                                                    fontSize: 12,
                                                    fontWeight: '400',
                                                    marginTop: -10,
                                                    display: 'block',
                                                }}
                                            >% BURNED</span>
                                        </Typography>
                                    </Grid>

                                    <Grid item xs={6} style={{marginTop: 20}}>
                                        <List>
                                            <ListItem>
                                                <ListItemAvatar>
                                                    <Avatar
                                                        sx={{bgcolor: project.primary}}><AccountBalanceIcon/></Avatar>
                                                </ListItemAvatar>
                                                <ListItemText
                                                    primary={NumberFormatter.format(stats.supply) + ` ${project.symbol}`}
                                                    secondary="Total Supply"/>
                                            </ListItem>
                                            <ListItem>
                                                <ListItemAvatar>
                                                    <Avatar
                                                        sx={{bgcolor: project.primary}}><LocalFireDepartmentIcon/></Avatar>
                                                </ListItemAvatar>
                                                <ListItemText
                                                    primary={NumberFormatter.format(stats.burnt) + ` ${project.symbol}`}
                                                    secondary="Burnt Supply"/>
                                            </ListItem>
                                            <ListItem>
                                                <ListItemAvatar>
                                                    <Avatar sx={{bgcolor: project.primary}}><CachedIcon/></Avatar>
                                                </ListItemAvatar>
                                                <ListItemText
                                                    primary={NumberFormatter.format(stats.supply - stats.burnt) + ` ${project.symbol}`}
                                                    secondary="Circulating Supply"/>
                                            </ListItem>
                                        </List>
                                    </Grid>
                                </Grid>
                            </Paper>
                        </Grid>
                        <Grid item xs={12}>
                            <Paper style={{padding: 20}}>
                                <Grid container>
                                    <Grid item xs={6}>
                                        <List>
                                            <ListItem>
                                                <ListItemAvatar>
                                                    <Avatar sx={{bgcolor: project.primary}}><AttachMoneyIcon/></Avatar>
                                                </ListItemAvatar>
                                                <ListItemText
                                                    primary={`$${NumberFormatter.format((stats.fbnb - stats.backing) / stats.liquidity * bnbPrice)}`}
                                                    secondary={`USD Price`}/>
                                            </ListItem>
                                            <ListItem>
                                                <ListItemAvatar>
                                                    <Avatar sx={{bgcolor: project.primary}}><BNBIcon/></Avatar>
                                                </ListItemAvatar>
                                                <ListItemText
                                                    primary={NumberFormatter.format(stats.price) + " BNB"}
                                                    secondary={`BNB Price`}/>
                                            </ListItem>
                                            <ListItem>
                                                <ListItemAvatar>
                                                    <Avatar sx={{bgcolor: project.primary}}><ShieldIcon/></Avatar>
                                                </ListItemAvatar>
                                                <ListItemText
                                                    primary={NumberFormatter.format(stats.backingPerToken) + " fBNB"}
                                                    secondary="Backing Per Token"/>
                                            </ListItem>
                                            <ListItem>
                                                <ListItemAvatar>
                                                    <Avatar sx={{bgcolor: project.primary}}><CalculateIcon/></Avatar>
                                                </ListItemAvatar>
                                                <ListItemText
                                                    primary={(((stats.fbnb - stats.backing) / stats.liquidity) / stats.backingPerToken).toFixed(2) + "x"}
                                                    secondary="Baseline To Market Factor"/>
                                            </ListItem>
                                        </List>
                                    </Grid>
                                    <Grid item xs={6}>
                                        <List>
                                            <ListItem>
                                                <ListItemAvatar>
                                                    <Avatar sx={{bgcolor: project.primary}}><AttachMoneyIcon/></Avatar>
                                                </ListItemAvatar>
                                                <ListItemText
                                                    primary={"$" + NumberFormatter.format(stats.circulation * (stats.fbnb - stats.backing) / stats.liquidity * bnbPrice)}
                                                    secondary={`Market Cap`}/>
                                            </ListItem>
                                            <ListItem>
                                                <ListItemAvatar>
                                                    <Avatar sx={{bgcolor: project.primary}}><InvertColorsIcon/></Avatar>
                                                </ListItemAvatar>
                                                <ListItemText
                                                    primary={NumberFormatter.format(stats.liquidity) + ` ${project.symbol}`}
                                                    secondary={`Liquidity ${project.symbol}`}/>
                                            </ListItem>
                                            <ListItem>
                                                <ListItemAvatar>
                                                    <Avatar sx={{bgcolor: project.primary}}><InvertColorsIcon/></Avatar>
                                                </ListItemAvatar>
                                                <ListItemText primary={NumberFormatter.format(stats.fbnb - stats.backing) + " fBNB"}
                                                              secondary="Liquidity fBNB"/>
                                            </ListItem>
                                            <ListItem>
                                                <ListItemAvatar>
                                                    <Avatar sx={{bgcolor: project.primary}}><ShieldIcon/></Avatar>
                                                </ListItemAvatar>
                                                <ListItemText primary={NumberFormatter.format(stats.backing) + " fBNB"}
                                                              secondary="Total Backing"/>
                                            </ListItem>
                                        </List>
                                    </Grid>
                                </Grid>
                            </Paper>
                        </Grid>
                    </Grid>
                </Grid>
            </>
        }
    </Grid>)
}

function BNBIcon(props) {
    return (
        <SvgIcon xmlns="http://www.w3.org/2000/svg" width="2500" height="2500" viewBox="0 0 2000 2000">
            <g fill="#fff">
                <path d="M611.59 840.42l388.4-388.39 388.6 388.59 226-226L999.99 0 385.6 614.42l225.99 226M.006 999.969l226.007-226.007 225.992 225.993L226 1225.96zM611.59 1159.58l388.4 388.39 388.59-388.58 226.12 225.88-.11.12L999.99 2000l-614.41-614.4-.32-.32 226.33-225.7M1548.013 1000.093l226.007-226.006 225.992 225.992-226.006 226.007z"/>
                <path d="M1229.22 999.88h.1L999.99 770.55 830.51 940.03h-.01l-19.47 19.48-40.16 40.17-.32.31.32.33 229.12 229.13 229.33-229.33.11-.13-.21-.11"/>
            </g>
        </SvgIcon>
    )
}