import Web3 from 'web3';

import promisify from "../utils/promisify";
import abi from '../abis/abi.json';
import sd_abi from '../abis/sd_abi.json';

import store from "../redux/store";
import {updateProjectsMap} from "../redux/slices/divvySlice";

const web3 = new Web3('https://bsc-dataseed.binance.org/');

// const divvyContract = '0xe9490A983292bD844074Ac3e83c3cb408f578b61';
const fBNBContract = '0x87b1AccE6a1958E522233A737313C086551a5c76';
const deadWallet = '0x000000000000000000000000000000000000dead';
const divvyWallet = '0xb80267eA7fa368374ee4d4Bf10044778232AdEFe';

const fBNBcontract = new web3.eth.Contract(abi, fBNBContract)

const getProjectStats = (contract_address) => {

    const contract = new web3.eth.Contract(sd_abi, contract_address);

    //balance of fBNB in project
    const getFBNB = promisify(cb => fBNBcontract.methods.balanceOf(contract_address).call(cb));
    //balance of project in Liquidity
    const getLiquidity = promisify(cb => contract.methods.getBalance(contract_address).call(cb));
    //balance of project in dead wallet
    const getBurnt = promisify(cb => contract.methods.balanceOf(deadWallet).call(cb));
    //total supply
    const getSupply = promisify(cb => contract.methods.totalSupply().call(cb));
    //asset backing
    const getBacking = promisify(cb => contract.methods._totalSupply5().call(cb));
    //backing per token
    const getBackingPerToken = promisify(cb => contract.methods.oneTokentoBacking().call(cb));
    //balance of tokens in Divvy Fund
    const getDivvyBalance = promisify(cb => contract.methods.balanceOf(divvyWallet).call(cb));

    Promise.all([
        getFBNB,
        getLiquidity,
        getBurnt,
        getSupply,
        getBacking,
        getBackingPerToken,
        getDivvyBalance
    ]).then(resp => {
        const fbnb = resp[0] / Math.pow(10, 18);
        const backing = resp[4] / Math.pow(10, 18);
        const liquidity = resp[1] / Math.pow(10, 9);
        const supply = resp[3] / Math.pow(10, 9);
        const burnt = resp[2] / Math.pow(10, 9);
        const circulation = supply - burnt;
        const price = (fbnb - backing) / liquidity;

        store.dispatch(updateProjectsMap({
            contract: contract_address,
            stats: {
                fbnb,
                liquidity,
                burnt,
                supply,
                circulation,
                backing,
                backingPerToken: resp[5] / Math.pow(10, 18),
                divvyBalance: resp[6] / Math.pow(10, 9),
                price
            }
        }));
    })
}

export default getProjectStats;